#root {
  font-size: 0.53333333rem;
}

input {
  width: 2.66666667rem;
  border: 0.02666667rem solid #d9d9d9;
  border-radius: 0.05333333rem;
  margin: 0 0.26666667rem;
  flex: 1;
}

input:focus {
  border-color: #40a9ff;
  box-shadow: 0 0 0 0.05333333rem rgba(24, 144, 255, 0.2);
  outline: 0;
}

.btn {
  padding: 0.18666667rem 0.32rem;
  background-color: #1677ff;
  border-radius: 0.13333333rem;
  color: #fff;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  display: inline-block;
  position: relative;
}

.btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0;
}

.btn.danger {
  background-color: #ff3141;
}

.btn:active::before {
  opacity: .08;
}

.square {
  display: flex;
  justify-content: space-between;
  margin: 0.8rem 0;
}

.square .square-content {
  flex: 1;
}

.square .square-content .square-item {
  margin-bottom: 0.26666667rem;
  display: flex;
}

.circular {
  display: flex;
}

.circular .circular-content {
  flex: 1;
}

.circular .circular-item {
  margin-bottom: 0.26666667rem;
  display: flex;
}

.results {
  height: 5.33333333rem;
  font-size: 0.53333333rem;
  margin-top: 0.53333333rem;
  overflow-y: auto;
}

.total {
  font-size: 0.8rem;
  font-weight: bold;
  margin-top: 0.53333333rem;
}

.btns-warp {
  text-align: center;
}

.btns-warp .btn {
  margin-right: 0.26666667rem;
}

.btns-warp .btn:last-child {
  margin-right: 0;
}