.am-icon {
  fill: currentColor;
  background-size: cover;
  width: 0.58666667rem;
  height: 0.58666667rem;
}

.am-icon-xxs {
  width: 0.4rem;
  height: 0.4rem;
}

.am-icon-xs {
  width: 0.48rem;
  height: 0.48rem;
}

.am-icon-sm {
  width: 0.56rem;
  height: 0.56rem;
}

.am-icon-md {
  width: 0.58666667rem;
  height: 0.58666667rem;
}

.am-icon-lg {
  width: 0.96rem;
  height: 0.96rem;
}

.am-icon-loading {
  -webkit-animation: cirle-anim 1s linear infinite;
  animation: cirle-anim 1s linear infinite;
}

@-webkit-keyframes cirle-anim {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes cirle-anim {
  100% {
    transform: rotate(360deg);
  }
}